<script setup lang="ts">
import { computed } from "@vue/reactivity";
import { onMounted, ref, type Ref } from "@vue/runtime-core";

import Papa from 'papaparse'

/*
onMounted(() => {
  let map = L.map("map").setView([51.505, -0.09], 13);

  L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      maxZoom: 19,
      attribution: '© OpenStreetMap'
  }).addTo(map);
})*/

type hospitalT = {
  "id": number,
  "name": string,
  "address": string,
  "telephone": string,
  "longitude": number,
  "latitude": number,
  "url": string
}


type clinicT = {
  name: string, 
  address: string,
  telephone: string
}

type citysT = Record<string, string[]>;

const hospitals: Ref<Record<string, hospitalT>> = ref({});
const selected: Ref<string> = ref("");

const citys: Ref<citysT> = ref({});
const city = ref("");
const district = ref("");
const clinic = ref("");

const clinics: Ref<Array<clinicT>> = ref([]);

const clinicFilter = computed(() => 
  district.value ? clinics.value.filter((v, i, a) => v.address.indexOf(district.value) != -1): []
);

const hospitalurl = computed(() => 
"https://maps.google.com/maps?q=" + clinic.value + "&t=&z=17&ie=UTF8&iwloc=&output=embed"
)


fetch("administrativeArea.json")
  .then((value) => value.json())
  .then((value) => {
    citys.value = value;
  });


fetch("clinicList.csv").then(value => value.text()).then(text => {
  const data = Papa.parse(text).data as Array<string>;
  for(let v of data) {
    let c: clinicT = {
    name: v[0],
    address: v[1],
    telephone: v[2]
}
    clinics.value.push(c)
    // console.log(v);

  }
});


fetch("hospitals.json").then(value => value.json()).then(value => {
  hospitals.value = value;
});






</script>

<template>

  <div class="form-row">
    <div class="col-sm-12 form-inline">
      戶籍住址：
      <select class="form-control" id="city" name="city" v-model="city" required>
        <option v-for="(v, i) in citys">{{ i }}</option>
      </select>
      <select class="form-control" id="district" name="district" v-model="district" required>
        <option v-for="(v, i) in citys[city]" :key="v">{{ v }}</option>
      </select>
      <select class="form-control" id="clinic" name="clinic" v-model="clinic" required>
        <option v-for="(v, i) in clinicFilter" :key="v.name">{{ v.name + " - " + v.address }}</option>
      </select>
    </div>
  </div>

  <!-- 
  <select class="form-control" id="selectHospital" name="selectHospital" v-on:change="onchange" v-model="selected">
    <option v-for="(v, k, a) in hospitals" :value="k">{{ k }}</option>
  </select>
  -->
  <iframe v-if="clinic" :src="hospitalurl" width="100%" height="480" style="border:0;" loading="lazy"
    referrerpolicy="no-referrer-when-downgrade"></iframe>


</template>

<style>
#map {
  height: 400px;
}
</style>
